.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.App-main {

}
.inpword{
  padding: 15px;
  width: 300px;
  outline: none;
  border: 2px solid #ffffff;
  background-color: #282c34;
  color: #fff;
  font-size: 18px;
  font-weight: bold;
}
.inpword:hover{
  border: 2px solid #61dafb;
}
.btnsubmit{
  padding: 15px;
  color: #fff;
  border: 2px solid #ffffff;
  background-color: #282c34;
  color: #fff;
  font-size: 18px;
  font-weight: bold;
  margin-left: 5px;
  cursor: pointer;
}
.btnsubmit:hover{
  border: 2px solid #61dafb;
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
